edby=\"navbarDropdown\"\n          >\n            <button class=\"dropdown-item text-right\" @click=\"logout\">\n              Logout\n            </button>\n          </div>\n        </li>\n      </ul>\n    </nav>\n  </div>\n</template>\n\n<script>\nimport { mapGetters } from \"vuex\";\nimport store from '../../store';\nexport default {\n  data() {\n    return {\n      siteName: null,\n    };\n  }, //end of data\n\n  computed: {\n    ...mapGetters({\n      user: \"adminAuth/user\",\n      isLoggedIn: \"adminAuth/isLoggedIn\",\n    }),\n  }, //end of computed\n\n  methods: {\n    logout() {\n      axios.post(\"/api/admin/logout\", this.user).then((response) => {\n        if (response.status == 200) {\n          store.commit(\"adminAuth/logout\");\n          this.$router.push({ name: \"admin.login\" });\n        }\n      });\n    }, //end of logout\n\n    retriveSiteName() {\n      axios\n        .get(\"/api/admin/settings\")\n        .then((response) => {\n          this.siteName = response.data.site_name;\n        })\n        .catch((error) => {\n          console.log(error);\n        });\n    }, //end of retrive siteName\n  }, //end of methods\n\n  created() {\n    this.retriveSiteName();\n  }, //end of created\n};\n</script>\n\n<style scoped>\n/*\n   * Navbar\n   */\n.navbar-brand {\n  padding-top: 0.75rem;\n  padding-bottom: 0.75rem;\n  font-size: 1rem;\n}\n\n.navbar .navbar-toggler {\n  top: 0.25rem;\n  right: 1rem;\n}\n\n.navbar .form-control {\n  padding: 0.75rem 1rem;\n  border-width: 0;\n  border-radius: 0;\n}\n\n.form-control-dark {\n  color: #fff;\n  background-color: rgba(255, 255, 255, 0.1);\n  border-color: rgba(255, 255, 255, 0.1);\n}\n\n.form-control-dark:focus {\n  border-color: transparent;\n  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.25);\n}\n</style>"],"sourceRoot":""}]);
